<script lang="coffeescript">
/*

  events =
    "key": (e)->
    ...

*/
var component, dispatch, isShowing, tippyObject, tippyPopover, updatedOptions;

import {
  createEventDispatcher
} from 'svelte';

dispatch = createEventDispatcher();

import tippy from 'tippy.js';

export var popover = void 0; // svelte template for the popover

export var options = {}; // options to pass to the popover

export var style = void 0;

export var classes = void 0;

export var disabled = false;

isShowing = false;

export var events = {};

tippyObject = void 0;

component = void 0;

// todo:
// fill

tippyPopover = function(element, content) {
  var tippyOptions;
  tippyOptions = {
    theme: "menu",
    appendTo: "parent",
    trigger: "click",
    placement: "bottom-end",
    interactive: true,
    popperOptions: {
      modifiers: [
        {
          name: "flip",
          options: {
            fallbackPlacements: ["top",
        "right",
        "left"]
          }
        }
      ]
    }
  };
  if (typeof content === 'function') {
    tippyOptions.onCreate = function(instance) {
      component = new content({
        target: instance.popper.querySelector('.tippy-content')
      });
      component.$set({
        options: options
      });
      //console.log "tipy set optoins = "
      //console.log options
      Object.keys(events).forEach(function(eventKey) {
        return component.$on(eventKey, function(e) {
          tippyObject.hide();
          return events[eventKey](e);
        });
      });
    };
    tippyOptions.onHide = function(instance) {
      //console.error "TIPPY HIDE"
      isShowing = false;
      return component.$set({
        isShowing: isShowing
      });
    };
    tippyOptions.onShow = function(instance) {
      //console.error "TIPPY SHOW"
      isShowing = true;
      return component.$set({
        isShowing: isShowing
      });
    };
  } else {
    tippyOptions.content = content;
  }
  if (Object.keys(tippyOptions).length === 0) {
    tippyObject = tippy(element);
  } else {
    tippyObject = tippy(element, tippyOptions);
  }
  return {
    update: function(params) {
      console.error("update");
    },
    // if component
    //  component.$set { value: selectedDate }
    destroy: function() {
      tippyObject.destroy();
      if (component) {
        // debugger
        component.$destroy();
      }
    }
  };
};

updatedOptions = function(newOptions) {
  if (component != null) {
    //console.log "tipy updatedOptions optoins = "
    //console.log options
    return component.$set({
      options: newOptions
    });
  }
};

$: updatedOptions(options);
</script>

<button type="button" on:click {style} {disabled} use:tippyPopover={popover} class={classes}><slot/></button>

<style lang="sass">button {
  display: inline-block;
  padding: 0px;
  background: none;
  height: 24px;
  min-width: 24px;
  width: 24px;
  border: none;
  margin: 0px;
}
button:hover {
  background: none;
}</style>